/* App.css */

body {
  margin: 0;
  font-family: Arial, sans-serif;
}

.app {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.header {
  background-color: #1DB954;
  color: white;
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.title {
  font-size: 24px;
}

.nav {
  display: flex;
}

.nav-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav-item {
  margin-right: 20px;
  cursor: pointer;
}

.main {
  flex-grow: 1;
  padding: 20px;
}

.about-section,
.projects-section,
.contact-section {
  margin-bottom: 40px;
}

.footer {
  background-color: #282828;
  color: white;
  padding: 20px;
  text-align: center;
}
